<template>
  <div class="orders">
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" class="input-with-select">
            <el-button slot="append" icon="el-icon-search" @click="getOrderList()"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderData" style="width: 100%" border>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scop">
            <el-tag v-if="scop.row.order_pay==0" type="danger"></el-tag>
            <el-tag v-else type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scop">{{scop.row.create_time | forDate}}</template>
        </el-table-column>
        <el-table-column label="">
          <template>
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="editDia = true"></el-button>
            <el-button type="success" icon="el-icon-location" size="mini" @click="kuaiid"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[5, 10, 25, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!-- -->
      <el-dialog title="" :visible.sync="kuaidiDia" width="30%">
        <el-steps direction="vertical" :active="1">
          <el-step v-for="(item,index) in kuaiidData" :key="index" :title="item.time" :description="item.context"></el-step>
        </el-steps>
      </el-dialog>

      <!--  -->
    <el-dialog title="" :visible.sync="editDia" width="50%" @closed="diaClosed('edutRef')">
      <el-form :model="editForm" :rules="editRul" ref="edutRef">
        <el-form-item label="/" label-width="80px">
          <el-cascader
            v-model="editVal"
            :options="cityOptions"
            :props="{ expandTrigger: 'hover' }"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="name" label-width="80px">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDia = false"> </el-button>
        <el-button type="primary" @click="editDia = false"> </el-button>
      </span>
    </el-dialog>
    </el-card>
  </div>
</template>

<script>
import cityOptions from "./js/city_data2017_element";
export default {
  data() {
    return {
      orderData: [],
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      kuaidiDia: false,
      kuaiidData : [],
      // 
      editDia: false,
      editForm: {
        name: ""
      },
      editRul: {
        name: [{ required: true, message: "", trigger: "blur" }]
      },
      // 
      editVal: [],
      // 
      cityOptions: cityOptions
    };
  },
  methods: {
    async getOrderList() {
      let { data: res } = await this.$http.get("orders", {
        params: this.queryInfo
      });
      this.total = res.data.total;

      this.orderData = res.data.goods;
    },
    handleSizeChange(size) {
      this.queryInfo.pagesize = size;
      this.getOrderList();
    },
    handleCurrentChange(num) {
      this.queryInfo.pagenum = num;
      this.getOrderList();
    },
    async kuaiid() {
      this.kuaidiDia = true;
      let {data:res} = await this.$http.get("/kuaidi/619915933338");
      if(res.data.length == 1){
          return this.kuaiid()
      }
      this.kuaiidData = res.data
    }
  },
  created: function() {
    this.getOrderList();
  }
};
</script>

